.title
{
    color: white;
    font-family: 'Orbitron', sans-serif;
    font-size: 4rem;
    margin-top: 1rem;
    margin-bottom: 10px;
}

.thick-h2
{
    width: 100%;
    border-bottom: 3px white solid;
}


hr
{
    width: 25%;
    border-bottom: 1px white solid;
    margin-top: 10px;
}

.button
{
    border-radius: 7px;
    font-family: 'Josefin Sans', sans-serif;
    font-weight: bold;
    font-size: 1.5rem;
    border-style: solid;
    text-align: center;
}

.transperent-button.light
{
    background-color: transparent;
    color: black;
    border-color: black;
}

.transperent-button
{
    background-color: transparent;
    color: white;
    border-color: white;
}   

.filled-button.light
{
    background-color: black;
    color: white;
    margin: 2rem 0 0.5rem 0;
    border-color: black;
}

.filled-button
{
    background-color: white;
    color: black;
    margin: 2rem 0 0.5rem 0;
    border-color: white;
}

.vertical-hr
{
    border: none;
    border-left: 3px white solid;
    height: 100%;
    margin: 0;
}

.light
{
    color: black;
    background-color: white;
    border-color: black;
}
